.NavBar {
  display: -ms-flexbox;
  display: flex;
  background-color: #22181C;
  -ms-flex-direction: row;
      flex-direction: row;
  padding-left: 2%;
  padding-right: 5%;
  color: #F6E8EA;
}

nav a {
    color: #EFD9CE;
}

nav a:hover {
  color: #F75D5D;
}

.NavBar img {
  border-radius: 50%;
  width: 10%;
  height: 10%;
  -ms-flex-item-align: center;
      align-self: center;
}

h1 {
  padding-left: 3%;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 24px;
  -ms-flex-item-align: center;
      align-self: center;
}

@-webkit-keyframes colorslide {
  0% {
    color: #F75D5D;
  }
  50% {
    color: #D63AF9;
  }
  100% {
    color: #F75D5D;
  }
}

@keyframes colorslide {
  0% {
    color: #F75D5D;
  }
  50% {
    color: #D63AF9;
  }
  100% {
    color: #F75D5D;
  }
}

h1:hover {
  -webkit-animation: colorslide 2s;
          animation: colorslide 2s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;

}

nav {
  -ms-flex: 2 1;
      flex: 2 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-left: 15%;
  text-align: right;
}

nav a {
  font-size: 18px;
  text-decoration: none;
  -ms-flex-item-align: center;
      align-self: center;
}

@media only screen and (max-width: 680px) {
  h1 {
    font-size: 20px;
  }
  nav a {
    font-size: 14px;
  }
}

@media only screen and (min-width: 960px) {
  h1 {
    font-size: 32px;
  }

  nav a {
    font-size: 24px;
  }
}



